import { H0 } from "@/components/Typography/Heading.styles";
import PageContainer from "@/components/Main/PageContainer";
import PostCard from "@/components/PostCard/PostCard";
import PaginationBar from "@/components/Pagination/PaginationBar";
import { BlogDetails } from "@/types/blog";

async function getPosts() {
  console.log(
    `${process.env.NEXT_PUBLIC_SERVER_HOST}/blogs?pagination[page]=1&pagination[pageSize]=3&populate=*&sort=createdAt:desc`
  );
  const res = await fetch(
    `${process.env.NEXT_PUBLIC_SERVER_HOST}/api/blogs?pagination[page]=1&pagination[pageSize]=3&populate=*&sort=createdAt:desc`,
    { cache: "no-store" }
  );
  if (!res.ok) throw new Error("Failed to fetch posts");
  return res.json();
}

// SSR metadata cho trang blog chính
export async function generateMetadata() {
  try {
    const data = await getPosts();
    const posts: BlogDetails[] = data.data;

    const description =
      posts
        .map((p) =>
          Array.isArray(p.subContent)
            ? p.subContent.map((c) => c.content).join(" ")
            : p.subContent ?? ""
        )
        .join(" | ")
        .slice(0, 160) || "Blog về chủ đề thú vị";

    return {
      title: "Trang Blog",
      description,
      openGraph: {
        title: "Trang Blog",
        description,
        images: posts[0]?.cover?.url ? [{ url: posts[0].cover.url }] : [],
      },
    };
  } catch {
    return { title: "Trang Blog" };
  }
}

// Component SSR
export default async function Home() {
  let data;
  try {
    data = await getPosts();
  } catch {
    return <div>Error loading posts</div>;
  }

  const posts: BlogDetails[] = data.data;
  const pageCount = data.meta.pagination.pageCount;

  return (
    <PageContainer>
      <div className="flex-2 flex flex-col items-center gap-10 md:gap-[50px]">
        <H0>Blog</H0>

        <div className="flex w-full flex-col gap-[50px]">
          {posts.map((post, index) => (
            <PostCard index={index} post={post} key={post.documentId} />
          ))}
        </div>

        <PaginationBar
          currentPage={1}
          totalPages={pageCount}
          onPageChange={() => {}}
        />
      </div>
    </PageContainer>
  );
}
